import React, { Component } from 'react'
import Board from './Board.js';
import '../index.css'


class Tile extends React.Component {
  constructor(props) {
    super(props);
    this.name = this.props.name;
  }

  render() {
    return (
      <div className="card tile" onClick={this.props.onClick}>
        <h3 >{this.props.name}</h3>

      </div>

    );

  }


}

export default Tile